<template>
  <div class="wrap">
    <div id="map" class="block" :style="{height: height+'px'}"></div>
    <div id="gradient1" class="gradient"></div>
    <div id="pie" class="block" :style="{height: height+'px'}"></div>
    <div id="gradient2" class="gradient"></div>
    <div id="table">
      <el-table :data="data" stripe @row-click="rowClick"
                :header-cell-style='{textAlign: "center"}'
                :cell-style='{textAlign: "center"}'>
        <el-table-column label="Who" prop="name"></el-table-column>
        <el-table-column label="Where">
          <template slot-scope="scope">
            <span v-if="scope.row.where">{{scope.row.where}}</span>
            <span v-else class="red"></span>
          </template>
        </el-table-column>
        <el-table-column label="What">
          <template slot-scope="scope">
            <span v-if="scope.row.what">{{scope.row.what}}</span>
            <span v-else class="red"></span>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="info">lyh</div>
    <div class="footer">
      <span @click="showInfo()">  </span>
    </div>
  </div>
</template>

<style scoped lang="scss">
  .wrap{
    display: flex;
    flex-flow: column nowrap;
    justify-content: flex-start;
    align-content: center;
    .block{
      width: 100%;
    }
    #table{
      width: 100%;
      padding: 20px 10px 0;
      box-sizing: border-box;
      .red{
        color: red;
      }
    }
    .gradient{
      width: 100%;
      height: 60px;
    }
    #gradient1{
      background: linear-gradient(#555, #aaa);
    }
    #gradient2{
      background: linear-gradient(#aaa, #fff);
    }
    .info{
      margin: 40px 0 10px;
      text-align: center;
      color: #999;
      font-size: 14px;
      font-weight: bold;
    }
    .footer{
      text-align: center;
      font-size: 12px;
      font-weight: bold;
      color: #999;
      margin: 0 auto 10px;
      cursor: pointer;
    }
  }
</style>

<script>
  import allData from "./data.js"
  import "echarts/map/js/china.js"
  export default {
    name: "graduate",
    data(){
      return{
        height: 0,
        egg: 0,
      }
    },
    mixins: [allData],
    created(){
      document.title = "4/11 17:01";
    },
    mounted(){
      window.vuethis = this;

      this.height = window.innerHeight;

      let echarts = require('echarts');

      this.$nextTick(() => {
        let myChart1 = echarts.init(document.getElementById('map'));
        myChart1.setOption({
          title: {
            text: ' ',
            link: 'javascript: vuethis.showInfo();',
            target: 'self',
            textStyle: {
              color: '#888',
              fontSize: 12,
              fontWeight: 'bold',
              textBorderColor: '#555',
              textBorderWidth: 1,
              textShadowColor: '#555',
              textShadowBlur: 5
            },
            bottom: 0,
            left: 'center'
          },
          backgroundColor: '#555',
          tooltip: {
            trigger: 'item',
            formatter: function (params) {
              if(params.data.name=='') return '<br> `^`';
              if(params.data.name=='') return '';
              if(params.data.name=='') return '';
              if(params.data.name=='') return '';
              if(params.data.name=='') return '';
              if(params.data.name=='') return '';
              if(params.data.name=='') return '';
              if(params.data.name=='') return '';
              if(params.data.name=='') return '';
              var res;
              if(params.data.name=='') res = '';
              else res = params.data.name;
              res += ': ' + params.data.value + '<br>';
              for(let i=0;i<params.data.list.length;i++){
                res += params.data.list[i] + '<br>';
              }
              return res;
            },
            padding: [10],
            confine: true,
            extraCssText: 'box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.5);'
          },
          visualMap: {
            min: 0,
            max: 8,
            calculable: false,
            inRange: {
              //color: ['#646a7d', '#bbb931', '#ed3d3d']
              color: ['#888', '#d5d11b', '#f49408', '#e93022']
            },
            //text: ['15', '0'],
            textStyle: {
              color: '#fff'
            }
          },
          series: [{
            name: 'namehere',
            type: 'map',
            roam: true,
            map: 'china',
            scaleLimit: {
              min: 1,
              max: 15
            },
            itemStyle:{
              normal:{
                label:{
                  show: false,
                  position: ['50%', '50%'],
                  color: '#333'
                },
                borderColor: 'rgba(0,0,0,.3325)'
              },
              emphasis:{
                label:{show:false}
              }
            },
            data: this.getCityData()
          }]
        });

        let myChart3 = echarts.init(document.getElementById('pie'));
        myChart3.setOption({
          title: {
            text: ' vs ',
            link: 'javascript: vuethis.showInfo();',
            target: 'self',
            textStyle: {
              color: '#666',
              fontSize: 12,
              fontWeight: 'bold'
            },
            bottom: 0,
            left: 'center'
          },
          backgroundColor: '#aaa',
          tooltip : {
            trigger: 'item',
            formatter: "{b} : {c} ({d}%)"
          },
          legend: {
            orient: 'vertical',
            top: 'middle',
            left: 'left',
            data: ['','','','',''],
            inactiveColor: '#666'
          },
          series : [
            {
              name: '',
              type: 'pie',
              radius : ['30%', '55%'],
              center: ['60%', '50%'],
              avoidLabelOverlap: false,
              hoverOffset: 13,
              label: {
                normal: {
                  show: false,
                  position: 'center'
                },
                emphasis: {
                  show: true,
                  textStyle: {
                    fontSize: '30',
                    fontWeight: 'bold'
                  }
                }
              },
              labelLine: {
                normal: {
                  show: false
                },
                emphasis: {
                  show: false
                }
              },
              data: this.getPieData(),
              itemStyle: {
                emphasis: {
                  shadowBlur: 10,
                  shadowOffsetX: 0,
                  shadowColor: 'rgba(0, 0, 0, 0.5)'
                }
              }
            }
          ]
        });
      });
    },
    methods: {
      getCityData(){
        return this.whereMap.map((cityName, index) => {
          let list = [];
          this.data.forEach(n => {
            if(n.whereIndex === index){
              list.push(`${n.name}${n.what}`);
            }
          });
          return {name: cityName, value: list.length, list: list};
        });
      },
      getPieData(){
        return this.whatMap.map((whatName, index) => {
          return {value: this.data.filter(n => n.whatIndex === index).length, name: whatName};
        });
      },
      rowClick(row){
        if(row.name === ""){
          this.egg++;
          switch (this.egg){
            case 1:
              alert("");
              break;
            case 2:
              alert("");
              break;
            case 3:
              alert("");
              break;
            case 4:
              alert("...");
              break;
            case 5:
              alert("");
              break;
            case 8:
              alert("");
              break;
            case 18:
              alert("");
              break;
            case 30:
              alert("");
              break;
            case 50:
              alert("...");
              break;
            case 100:
              alert("100");
              break;
            case 200:
              alert("");
              break;
            case 500:
              alert("");
              break;
            case 1000:
              alert("");
              break;
            default:
              break;
          }
        }
      },
      showInfo(){
        alert(`
        1. pcovo
        2. 
        3. 233
        4. 
        5. 
        6. `);
      }
    }
  }
</script>



